@charset "UTF-8";
body {
  font-family: "Hiragino Kaku Gothic ProN";
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}

.banner {
  display: inline-block;
  background-color: peru;
  border-radius: 1.875rem;
}

.banner__inner {
  display: flex;
  width: 100%;
  align-items: center;
  padding-top: 0.3125rem;
  padding-bottom: 0.3125rem;
  padding-left: 0.9375rem;
  padding-right: 0.3125rem;
  justify-content: space-between;
}

.banner__logo {
  max-width: 6.25rem;
}

.banner__btn {
  margin-left: 1.5625rem;
}

.inner {
  width: 100%;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
}
@media screen and (min-width: 768px) {
  .inner {
    max-width: 1150px;
    padding-right: 25px;
    padding-left: 25px;
  }
}

.line-btn {
  display: inline-block;
  background-color: #05C755;
  color: #fff;
  padding: 0.625rem 1.25rem;
  border-radius: 2.5rem;
  font-size: 1rem;
  font-weight: 700;
  position: relative;
  max-width: 20.9375rem;
  width: 100%;
}

.line-btn::before {
  content: "";
  display: block;
  background-color: #fff;
  width: 0.125rem;
  height: 0.625rem;
  transform: rotate(-45deg);
  position: absolute;
  right: 0.5rem;
  top: 0.875rem;
}

.line-btn::after {
  content: "";
  display: block;
  background-color: #fff;
  width: 0.125rem;
  height: 0.625rem;
  transform: rotate(45deg);
  position: absolute;
  right: 0.5rem;
  bottom: 0.875rem;
}

.section-title {
  color: #DE2C25;
  font-size: 1.125rem;
  font-weight: 700;
  letter-spacing: -0.125rem;
}